<template>
  <div class="card" :style="value">
    <div class="imagen">
      <h2>n°{{pokemon.id_}}</h2>
      <img
        :src="pokemon.foto.front_default"
        alt="pokemon"
      />
    </div>
    <div class="data">
      <div class="nombre">
        <h2>{{pokemon.nombre}}</h2>
        <div class="otros">
          <div><h2>Tipo: {{pokemon.tipo}}</h2></div>
          <hr />
          <div><h2>Peso: {{pokemon.peso}}KG</h2></div>
        </div>

        <div class="footer">
          <small>@JavierBáez</small>
        </div>
      </div>
    </div>
  </div>
</template>

<script>

export default {
  name: "card",
  props: {
    pokemon: Object

  },
  data(){
    return{
      value: 'background: #ffffff'
    }
  },
  mounted(){
    this.bgCard(this.pokemon.tipo);
  },
  methods:{
    bgCard(tipo){
      if(tipo == 'grass'){
        const bg = require('@/assets/types/planta.png');
        this.value = "background-image: url("+bg+")"
      }
      if(tipo == 'fire'){
        const bg = require('@/assets/types/fuego.png');
        this.value = "background-image: url("+bg+")"
      }
      if(tipo == 'water'){
        const bg = require('@/assets/types/agua.png');
        this.value = "background-image: url("+bg+")"
      }
      if(tipo == 'bug'){
        const bg = require('@/assets/types/bicho.png');
        this.value = "background-image: url("+bg+")"
      }
      if(tipo == 'normal'){
        const bg = require('@/assets/types/normal.png');
        this.value = "background-image: url("+bg+")"
      }
      if(tipo == 'fairy'){
        const bg = require('@/assets/types/hada.png');
        this.value = "background-image: url("+bg+")"
      }
      if(tipo == 'ground'){
        const bg = require('@/assets/types/tierra.png');
        this.value = "background-image: url("+bg+")"
      }
      if(tipo == 'poison'){
        const bg = require('@/assets/types/veneno.png');
        this.value = "background-image: url("+bg+")"
      }
      if(tipo == 'electric'){
        const bg = require('@/assets/types/electrico.png');
        this.value = "background-image: url("+bg+")"
      }
      if(tipo == 'fighting'){
        const bg = require('@/assets/types/lucha.png');
        this.value = "background-image: url("+bg+")"
      }
      if(tipo == 'psychic'){
        const bg = require('@/assets/types/psyquico.png');
        this.value = "background-image: url("+bg+")"
      }
      if(tipo == 'rock'){
        const bg = require('@/assets/types/roca.png');
        this.value = "background-image: url("+bg+")"
      }
      if(tipo == 'ghost'){
        const bg = require('@/assets/types/fantasma.png');
        this.value = "background-image: url("+bg+")"
      }
      if(tipo == 'ice'){
        const bg = require('@/assets/types/hielo.png');
        this.value = "background-image: url("+bg+")"
      }
      if(tipo == 'dragon'){
        const bg = require('@/assets/types/dragon.png');
        this.value = "background-image: url("+bg+")"
      }
    }
  }
};
</script>

<style scoped>
.card {
  position: relative;
  text-align: center;
  /*background: linear-gradient(#e66465, #9198e5);*/
  /* background-image: url("https://images.pexels.com/photos/5749961/pexels-photo-5749961.jpeg?auto=compress&cs=tinysrgb&h=750&w=1260"); */
  background-position: center;
  width: 300px;
  height: 500px;
  border-radius: 5px;
  z-index: 1;
}

.imagen {
  text-align: center;
  width: 100%;
  height: 100%;
  padding-top: 5%;
}

.imagen h2 {
  color: white;
  padding: 0;
  margin: 0;
  z-index: 2;
  -webkit-text-stroke: 1px black;
}
.imagen img {
  max-width: 70%;
  max-height: 40%;
  
}
.data {
  position: absolute;
  background: rgba(0, 0, 0, 0.4);
  text-align: center;
  width: 90%;
  height: 60%;
  border-radius: 10px 10px 0px 0px;

  /* posicion vertical */
  bottom: 0px;

  /* centrado horizontal */
  margin-left: 5%;
  margin-right: 5%;
  z-index: -1;
}

.nombre {
  padding-top: 10%;
  padding-bottom: 0%;
  color: white;
}

.otros {
  display: flex;
  padding: 0% 5% 0% 5%;
}

.footer {
  width: 100%;
  position: absolute;
  bottom: 0px;
  margin: auto;
  padding-bottom: 5%;
}
</style>