<template>
  <div class="container">
    <div class="wrapper">
      <div v-for="pokemon in listadoPokemon" :key="pokemon.id">
        <card :pokemon="pokemon"></card>
      </div>
    </div>
  </div>
</template>

<script>
import axios from "axios";
import card from "@/components/card.vue";
export default {
  name: "pokemons",
  components: {
    card,
  },
  props: {},
  data(){
    return{
      listadoPokemon: [],
    }
  },
  mounted(){
    this.getPokemons();
    console.log(this.listadoPokemon)
  },
  methods: {
    async getPokemons() {
      try {
        for (let id = 80; id <=151; id++){
          const result = await axios('https://pokeapi.co/api/v2/pokemon/'+id);
            console.log(result)
            let poke = {
            id_: id,
            nombre: result.data.name,
            tipo: result.data.types[0].type.name,
            peso: result.data.weight,
            foto: result.data.sprites.other.dream_world
          }
          this.listadoPokemon.push(poke);
        }
      } catch (error) {
        console.log(error);
      }
    }
  }
};
</script>

